// portlet inside main content
.main__content {

	.portlet-group {
		.flexbox();
		.flex-wrap(wrap);

		@media(min-width: @md-min) {
			.flex-wrap(nowrap);
		}


		& > .portlet:first-child {
			width: 100%;

			@media(min-width: @md-min) {
				width: 60%;
				margin-right: 1.5rem;
			}

			.portlet__title,
			.portlet__description {
				color: @white;
			}

		}

		& > .portlet:last-child {
			width: 100%;

			@media(min-width: @md-min) {
				width: 40%;
			}

			.portlet__container:last-child {
				padding-bottom: 2rem;
			}
		}
	}

  p {
	color: @main-content;
  }

  .portlet h2,
  .portlet h4 {
	color: @blue-darkest;
  }

  h2 + p {
		margin-top: -6px;
	}

  .portlet-bill {
		h2 + p {
			margin-top: 0;
		}
	}

}

.main__subcontent {
  .flexbox();
  .flex-wrap(wrap);

  @media(min-width: @md-min) {
	.flex-wrap(nowrap);
  }

  .portlet-account {
	.portlet__content {
	  //padding-right: 6rem;
	}
  }

  & > .portlet {
	height: auto;
	position: relative;
	margin-right: 0;

	@media(min-width: @md-min) {
	  margin-right: 2rem;
	}

	&:last-child {
	  margin-right: 0;
	}

	.portlet__icon {
	  position: absolute;
	  right: 1.5rem;

	  @media(min-width: @md-min) {
		right: 0.5rem;
	  }

	}

	p {
	  margin-top: 0.66rem;
	  margin-bottom: 0.66rem;
	}
  }

}

.portlet__icon--alert {
	top: 12px;

	@media(min-width: @sm-min) {
		top: 0;
	}

}

.portlet-home {
	.portlet__description p {
		display: inline;
	}
}

.portlet-bill {
	.portlet__description p {
		display: inline;

		a {
			border-bottom: 1px solid;
		}
	}

	.portlet__title {
		margin-bottom: 0.66rem;
	}
	.portlet__description {
		margin-bottom: 0.66rem;
	}

}

// home page specific
.home {
	.portlet-home {
		.portlet__description {
			padding-right: 5rem;
		}
	}
}

.home div.portlet__description{
	padding-right: 5rem;
}

.about-us div.portlet__description,
.account div.portlet__description {
	margin-top: .66rem;
    margin-bottom: .66rem !important;
}

.page-sidebar > .portlet > a {
	height: 100%;
}